body {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
  background: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
li {
  font-family: "Nunito", sans-serif;
  margin: 0;
}
p,
span,
input,
a,
div {
  font-family: Roboto;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

.BrainhubCarousel__arrows {
  background-color: #02ab6b !important;
}

.BrainhubCarousel__arrows:hover:enabled,
.BrainhubCarousel__arrows:hover {
  background-color: #02ab6b !important;
}

.fallback {
  height: 90vh;
}
